.cpt-persons {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
}
.cpt-person-single {
  background-color: #fff;
  padding: 7px;
  border-radius: 4px;
}
.cpt-person-single .cpt-person-description p,
.cpt-person-single .cpt-person-excerpt p {
  padding-top: 0;
}
.cpt-person-single.row .cpt-person-portrait {
  float: left;
  margin-right: 10px;
}
.cpt-person-single.block {
  flex-grow: 1;
  flex-basis: 180px;
  margin-left: 5px;
  margin-right: 5px;
  margin-bottom: 15px;
}
.cpt-person-single.block .cpt-person-portrait,
.cpt-person-single.block header {
  text-align: center;
}
